.components-overview {
  max-width: 1100px;
  margin: 0 auto;
}

.overview-header {
  text-align: center;
  margin-bottom: 2rem;
  padding-bottom: 2rem;
  border-bottom: 1px solid var(--border-color);
}

.overview-title {
  font-family: var(--font-heading);
  font-size: var(--text-4xl);
  font-weight: 700;
  color: var(--text-primary);
  margin: 0 0 0.5rem 0;
}

.overview-subtitle {
  font-size: var(--text-lg);
  color: var(--text-secondary);
  margin: 0;
}

.search-container {
  margin-bottom: 2rem;
}

.search-box {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 0.75rem 1rem;
  background: var(--surface-card);
  border: 1px solid var(--border-color);
  border-radius: var(--border-radius-lg);
  transition: all var(--transition-fast);
}
.search-box:focus-within {
  border-color: var(--c3-accent);
  box-shadow: 0 0 0 3px rgba(0, 74, 153, 0.1);
}

.search-icon {
  color: var(--text-muted);
  font-size: 20px;
  width: 20px;
  height: 20px;
}

.search-input {
  flex: 1;
  border: none;
  background: transparent;
  font-family: var(--font-body);
  font-size: var(--text-base);
  color: var(--text-primary);
  outline: none;
}
.search-input::placeholder {
  color: var(--text-muted);
}

.search-clear {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.25rem;
  background: transparent;
  border: none;
  border-radius: var(--border-radius-sm);
  cursor: pointer;
  color: var(--text-muted);
  transition: all var(--transition-fast);
}
.search-clear:hover {
  background: var(--surface-elevated);
  color: var(--text-primary);
}
.search-clear .mat-icon {
  font-size: 18px;
  width: 18px;
  height: 18px;
}

.categories {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.category {
  animation: fadeInUp 0.4s ease-out both;
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.category-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 0.75rem 0;
  background: transparent;
  border: none;
  cursor: pointer;
  transition: opacity var(--transition-fast);
}
.category-header:hover {
  opacity: 0.8;
}

.category-info {
  display: flex;
  align-items: center;
  gap: 0.75rem;
}

.category-icon {
  color: var(--c3-primary);
  font-size: 24px;
  width: 24px;
  height: 24px;
}

.category-name {
  font-family: var(--font-heading);
  font-size: var(--text-xl);
  font-weight: 600;
  color: var(--text-primary);
  margin: 0;
}

.category-count {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  min-width: 24px;
  height: 24px;
  padding: 0 0.5rem;
  background: var(--surface-elevated);
  border-radius: 9999px;
  font-size: var(--text-xs);
  font-weight: 600;
  color: var(--text-secondary);
}

.expand-icon {
  color: var(--text-muted);
  transition: transform var(--transition-fast);
}
.expand-icon.rotated {
  transform: rotate(-90deg);
}

.category-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 1rem;
  margin-top: 1rem;
  padding-bottom: 1rem;
}

.component-card {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1rem;
  background: var(--surface-card);
  border: 1px solid var(--border-color);
  border-radius: var(--border-radius-lg);
  text-decoration: none;
  transition: all var(--transition-fast);
  animation: fadeIn 0.3s ease-out both;
}
.component-card:hover {
  border-color: var(--c3-primary);
  box-shadow: var(--shadow-md);
  transform: translateY(-2px);
}
.component-card:hover .card-arrow {
  transform: translateX(4px);
  color: var(--c3-primary);
}
.component-card:hover .card-icon {
  background: var(--c3-primary);
}
.component-card:hover .card-icon .mat-icon {
  color: white;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: scale(0.95);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}
.card-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 48px;
  height: 48px;
  background: var(--sidebar-link-active);
  border-radius: var(--border-radius-md);
  flex-shrink: 0;
  transition: all var(--transition-fast);
}
.card-icon .mat-icon {
  font-size: 24px;
  width: 24px;
  height: 24px;
  color: var(--c3-primary);
  transition: color var(--transition-fast);
}

.card-content {
  flex: 1;
  min-width: 0;
}

.card-header {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 0.25rem;
}

.card-title {
  font-family: var(--font-heading);
  font-size: var(--text-base);
  font-weight: 600;
  color: var(--text-primary);
  margin: 0;
}

.card-badge {
  display: inline-flex;
  align-items: center;
  padding: 0.125rem 0.5rem;
  font-size: 10px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  border-radius: 9999px;
}
.card-badge.badge-new {
  background: linear-gradient(135deg, var(--c3-primary) 0%, var(--c3-primary-dark) 100%);
  color: white;
}
.card-badge.badge-updated {
  background: var(--c3-accent);
  color: white;
}
.card-badge.badge-beta {
  background: var(--color-warning);
  color: white;
}

.card-description {
  font-size: var(--text-sm);
  color: var(--text-secondary);
  margin: 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.card-arrow {
  color: var(--text-muted);
  font-size: 18px;
  width: 18px;
  height: 18px;
  flex-shrink: 0;
  transition: all var(--transition-fast);
}

.no-results {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 4rem 2rem;
  text-align: center;
  color: var(--text-muted);
}
.no-results .mat-icon {
  font-size: 48px;
  width: 48px;
  height: 48px;
  margin-bottom: 1rem;
  opacity: 0.5;
}
.no-results p {
  margin: 0 0 1rem;
  font-size: var(--text-lg);
}

@media (max-width: 768px) {
  .overview-header {
    text-align: left;
  }
  .overview-title {
    font-size: var(--text-3xl);
  }
  .category-grid {
    grid-template-columns: 1fr;
  }
  .component-card {
    padding: 0.875rem;
  }
  .card-icon {
    width: 40px;
    height: 40px;
  }
  .card-icon .mat-icon {
    font-size: 20px;
    width: 20px;
    height: 20px;
  }
}
@media (max-width: 480px) {
  .card-description {
    display: none;
  }
  .card-arrow {
    display: none;
  }
}
